// common
@mixin time-margin {
  margin-right: 10px;
}
@mixin margin-center {
  margin: 0 auto;
}
@mixin text-center {
  text-align: center;
}

.now-time-wrapper,
.left-time-wrapper,
.time-setter-wrapper {
  @include text-center;
  margin-top: 15px;
}

.now-time,
.left-time {
  @include time-margin;
}

.now-time-wrapper,
.left-time-wrapper {
  font-size: 20px;
}

.time-setter-wrapper {
  .time-setter {
    margin: 20px 0;
  }
}

.left-time-wrapper {
}

.control-btn-list {
  margin-top: 10px;
  button {
    margin-right: 10px;
    &:last-child {
      margin-right: 0;
    }
  }
}
